import fs from "fs"
import process from "process"
import { lazy } from "../../core/Lazy"
import { Maybe } from "../../fe/MaybeTypes"
import { ancestors } from "../fs/Ancestors"
import { statMaybe } from "../fs/Stat"
import { isWin } from "../platform/Platform"

const getUid = lazy(() => process.geteuid?.())
const getGids = lazy(() => process.getgroups?.())

export function isStatRWX(stat: Maybe<fs.Stats>): boolean {
  return access({ stat, r: true, w: true, x: true })
}
export function isStatRX(stat: Maybe<fs.Stats>): boolean {
  return access({ stat, r: true, x: true })
}

export async function canRWXFirstExistingAncestor(
  nativePath: string
): Promise<boolean> {
  let foundNearest = false
  // We need to be able to rX all the way up to the root:
  for (const dir of [nativePath, ...ancestors(nativePath)]) {
    const s = await statMaybe(dir)
    if (s != null) {
      if (!s.isDirectory()) {
        return false
      }
      if (!foundNearest) {
        foundNearest = true
        if (!isStatRWX(s)) {
          return false
        }
      } else {
        if (!isStatRX(s)) {
          return false
        }
      }
    }
  }
  return true
}

export function access({
  stat,
  r = false,
  w = false,
  x = false,
  processUid,
  processGid
}: {
  stat: Maybe<fs.Stats>
  r?: boolean
  w?: boolean
  x?: boolean
  processUid?: number
  processGid?: number
}): boolean {
  if (stat == null) return false
  const uid = processUid ?? getUid()
  const gids = processGid != null ? [processGid] : getGids() ?? []

  const isRoot = uid === 0
  const isUser = isRoot || stat.uid === uid
  const isGroup = isRoot || gids.includes(stat.gid)

  if (r) {
    const mask = (isUser ? 0o400 : 0) | (isGroup ? 0o040 : 0) | 0o004
    if ((mask & stat.mode) === 0) return false
  }

  if (w) {
    const mask = (isUser ? 0o200 : 0) | (isGroup ? 0o020 : 0) | 0o002
    if ((mask & stat.mode) === 0) return false
  }

  // windows doesn't care about x: it happily executes files with no execute
  // bit as long as the extension is .exe/.bat/...
  if (x && !isWin) {
    const mask = (isUser ? 0o100 : 0) | (isGroup ? 0o010 : 0) | 0o001
    if ((mask & stat.mode) === 0) return false
  }

  return true
}
